@font-face {
  font-family: "Montserrat";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-family: "Montserrat", "Arial", sans-serif;
  line-height: 28px;
  scroll-behavior: smooth;
}
.page__body {
  display: grid;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: #FFFFFF;
  color: #2C2E3F;
}
.button {
  position: relative;
  display: block;
  width: 288px;
  margin: 0;
  padding: 19px 40px;
  font-size: 10px;
  line-height: 12px;
  font-weight: 500;
  letter-spacing: 2px;
  text-align: center;
  color: #FFFFFF;
  background-color: #FE7865;
  text-transform: uppercase;
  text-decoration: none;
  text-shadow: none;
  border: none;
}
.button:active,
.button:hover {
  background-color: #F17D4B;
}
@media (min-width: 768px) {
  .button {
    width: 250px;
  }
}
@media (min-width: 1170px) {
  .button {
    padding: 30px;
    font-size: 12px;
    line-height: 15px;
  }
  .button--form {
    width: 176px;
    padding: 18px 40px;
  }
}
.button--link {
  box-sizing: border-box;
  text-align: start;
}
@media (min-width: 1170px) {
  .button--link {
    width: 340px;
  }
}
.button--link::after {
  content: "";
  position: absolute;
  top: 18px;
  right: 40px;
  width: 14px;
  height: 14px;
  background-image: url("../img/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 1170px) {
  .button--link::after {
    top: 30px;
    right: 30px;
  }
}
.main-nav {
  position: relative;
  min-height: 50px;
  color: #48494D;
}
.main-nav--closed .main-nav__toggle {
  background-color: #FE7865;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 15px;
  width: 22px;
  height: 2px;
  background-color: #FFFFFF;
  box-shadow: 0 7px 0 0 #ffffff, 0 14px 0 0 #ffffff;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle::before {
    right: 25px;
  }
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 22px;
  right: 5px;
  width: 36px;
  height: 2px;
  background-color: rgba(20, 21, 23, 0.2);
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  right: 5px;
  width: 36px;
  height: 2px;
  background-color: rgba(20, 21, 23, 0.2);
  transform: rotate(45deg);
}
@media (max-width: 1169px) {
  .main-nav--opened .main-nav__list {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav {
    margin-top: 50px;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
    padding: 50px 0;
    margin-left: auto;
    height: auto;
  }
}
@media (min-width: 1170px) {
  .main-nav {
    min-height: 100px;
  }
  .main-nav--closed .main-nav__list,
  .main-nav--nojs .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    min-height: 100px;
    padding: 0;
    color: #141517;
  }
  .main-nav--closed .main-nav__item,
  .main-nav--nojs .main-nav__item {
    margin: 0;
    margin-right: 40px;
    background: none;
    padding: 0;
  }
  .main-nav--closed .main-nav__item a,
  .main-nav--nojs .main-nav__item a {
    text-decoration: none;
    color: inherit;
    font-size: 14px;
    line-height: 17px;
    font-weight: 500;
  }
  .main-nav--closed .main-nav__item a:hover,
  .main-nav--nojs .main-nav__item a:hover,
  .main-nav--closed .main-nav__item a:focus,
  .main-nav--nojs .main-nav__item a:focus {
    color: #F17D4B;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 50px;
  background-color: inherit;
  z-index: 5;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    width: 72px;
  }
}
@media (min-width: 1170px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 320px;
  z-index: 2;
  background-color: #FFFFFF;
  padding: 134px 0;
}
.main-nav__item {
  position: relative;
  align-items: stretch;
  padding: 16px;
  margin-bottom: 30px;
  background-color: #FBF8F8;
}
.main-nav__item::after {
  content: "";
  position: absolute;
  top: 17px;
  right: 18px;
  width: 25px;
  height: 25px;
  background-image: url("../img/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 1170px) {
  .main-nav__item::after {
    display: none;
  }
}
.main-nav__item a {
  text-decoration: none;
  color: inherit;
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
}
.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 320px;
  height: 568px;
  background-color: #FFFFFF;
  overflow: auto;
  z-index: 10;
}
@media (min-width: 768px) {
  .popup {
    width: 720px;
    height: 444px;
  }
}
.popup--closed {
  display: none;
}
.popup--opened {
  display: block;
}
.popup__container {
  position: relative;
}
@media (min-width: 768px) {
  .popup__container {
    display: flex;
    justify-content: flex-end;
    height: 100%;
    padding: 0;
    background-image: url("../img/popup.jpg");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: contain;
  }
}
.popup__close {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 50px;
  background-color: inherit;
  border: none;
  z-index: 5;
  cursor: pointer;
}
.popup__close::after,
.popup__close::before {
  content: "";
  position: absolute;
  top: 22px;
  right: 5px;
  width: 36px;
  height: 2px;
  background-color: rgba(20, 21, 23, 0.2);
}
.popup__close::after {
  transform: rotate(-45deg);
}
.popup__close::before {
  transform: rotate(45deg);
}
@media (min-width: 768px) {
  .popup__form {
    width: 390px;
  }
}
.popup__form-intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 16px 100px;
}
@media (min-width: 768px) {
  .popup__form-intro {
    padding: 72px 60px 89px;
  }
}
.popup__title {
  padding: 0;
  margin: 0;
  margin-bottom: 50px;
  font-size: 25px;
  line-height: 32px;
  font-weight: 600;
  text-align: center;
  color: #48494D;
}
.popup__contact {
  margin: 0;
  padding: 0;
  border: none;
}
.popup__input {
  width: 100%;
  height: 20px;
  margin-bottom: 20px;
  padding: 15px;
  box-sizing: border-box;
}
.popup__success {
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  border: solid 1px;
  background-color: #FBF8F8;
}
.popup__success-text {
  font-size: 25px;
  line-height: 43px;
  font-weight: 500;
  text-align: center;
  color: #48494D;
}
.page-footer {
  display: grid;
  margin: 59px 16px 26px;
}
